<template>
  <div>
    <el-form ref="form" label-width="100px">
      <el-tabs v-model="activeName">
        <el-tab-pane label="" name="email">
          <email/>
        </el-tab-pane>
        <el-tab-pane label="" name="aliYun">
          <el-row>
            <el-col :span="16">
              <ali-yun/>
            </el-col>
          </el-row>
        </el-tab-pane>
        <el-tab-pane label="" name="aliPay">
          <el-row>
            <el-col :span="16">
              <ali-pay/>
            </el-col>
          </el-row>
        </el-tab-pane>
        <el-tab-pane label="" name="upload">
          <el-row>
            <el-col :span="16">
              <upload/>
            </el-col>
          </el-row>
        </el-tab-pane>

        <el-tab-pane label="" name="user">
          <el-row>
            <el-col :span="16">
              <user/>
            </el-col>
          </el-row>
        </el-tab-pane>

        <el-tab-pane label="" name="notification">
          <el-row>
            <el-col :span="16">
              <notification/>
            </el-col>
          </el-row>
        </el-tab-pane>

        <el-tab-pane label="" name="maintain">
          <el-row>
            <el-col :span="16">
              <maintain/>
            </el-col>
          </el-row>
        </el-tab-pane>

      </el-tabs>
      <el-form-item class="mt-3">
        <el-button type="primary" @click="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  import AliPay from './components/AliPay'
  import AliYun from './components/AliYun'
  import Email from './components/Email'
  import Maintain from './components/Maintain'
  import Notification from './components/Notification'
  import Upload from "./components/Upload";
  import User from "./components/User";
  import store from '@/store/index'
  import {mapState} from 'vuex'

  export default {
    async beforeRouteEnter(to, from, next) {
      let response = await window.axios.get(`/site/config/${to.params.id}`);
      await store.commit('site/config', response.data.data);
      next();
    },
    data() {
      return {
        activeName: 'email'
      }
    },
    components: {
      Email, AliPay, AliYun, Maintain, Notification, Upload, User
    },
    computed: {
      ...mapState('site', ['config'])
    },
    methods: {
      async submit() {
        await window.axios.put(`/site/config/${this.$route.params.id}`, {
          data: this.config
        });
        this.$message.success('');
        this.$router.push('/admin')
      }
    }
  }
</script>
